import React from 'react';
import Breadcrumbs, { BreadcrumbsProps } from './Breadcrumbs';
import { defaultTransformer } from './methods';

/*
 * Representation of how breadcrumbs are received from Yext Streams
 */
export interface StreamsBreadcrumbs {
  dm_directoryParents: Array<StreamsBreadcrumb>
}

interface StreamsBreadcrumb {
  slug: string;
  name: string,
}

export interface BreadcrumbsPropsStreams extends BreadcrumbsProps {
  streamsBreadcrumbs: StreamsBreadcrumbs;
}

/*
 * Wrapper for the Breadcrumbs component which automatically processes Yext Streams data
 */
const YextBreadcrumbs = (props: BreadcrumbsPropsStreams) => {
  const { streamsBreadcrumbs } = props;
  const breadcrumbs = defaultTransformer(streamsBreadcrumbs);

  return <Breadcrumbs breadcrumbs={breadcrumbs} {...props} />;
}

export default YextBreadcrumbs;