import React from 'react';
import { StandardProps } from '../../types';
import { addressForCountry } from './i18n';
import { Address } from "@yext/types";
import Link  from "../Link";

type AddressPart = "line1" | "line2" | "line3" | "postalCode" | "city" | "regionCode" | "region" | 
                  "country" | "countryCode" | "extraDescription" | "sublocality"

export interface AddressProps extends StandardProps {
  address: Address,
  linkToGetDirections?: string,
  lines?: Array<Array<AddressPart>>;
}

export type AddressLineProps = {
  addressProps: AddressProps, // passthrough
  line: Array<string>,
}


const Address = (props: AddressProps) => {
  const { address, linkToGetDirections, yaTrack } = props;

  // make a function to get set all country's default linesDefault
  let linesDefault = addressForCountry(props.address?.countryCode);
  let lines = props?.lines ? props.lines : linesDefault;

  const addressDom = lines.map(line => <AddressLine addressProps={props} line={line} />)

  let addressEl = 
      <div className="Address-content" data-country={address.countryCode}>
        {addressDom}
      </div>;
  
  if (linkToGetDirections) {
    addressEl = 
      <Link className="Address-getDirectionLink" href={linkToGetDirections} yaTrack={yaTrack}>
        {addressEl}
      </Link>;
  }

  return addressEl;
};

const AddressLine = (props: AddressLineProps) => {
  let lineDom: JSX.Element[] = [];
  props?.line?.map(field => {
    if (field == 'city') {
      lineDom.push(<AddressCity address={props?.addressProps?.address} />);
    } else if (field == 'country') {
      lineDom.push(<AddressCountry address={props?.addressProps?.address} abbr={false} />);
    } else if (field == 'countryCode') {
      lineDom.push(<AddressCountry address={props?.addressProps?.address} abbr={true} />);
    } else if (field == 'extraDescription') {
      lineDom.push(<AddressExtraDescription address={props?.addressProps?.address} />);
    } else if (field == 'line1') {
      lineDom.push(<AddressLine1 address={props?.addressProps?.address} />);
    } else if (field == 'line2') {
      lineDom.push(<AddressLine2 address={props?.addressProps?.address} />);
    } else if (field == 'line3') {
      lineDom.push(<AddressLine3 address={props?.addressProps?.address} />);
    } else if (field == 'postalCode') {
      lineDom.push(<AddressPostalCode address={props?.addressProps?.address} />);
    } else if (field == 'region') {
      lineDom.push(<AddressRegion address={props?.addressProps?.address} />);
    } else if (field == 'regionCode') {
      lineDom.push(<AddressRegion address={props?.addressProps?.address} abbr={true} />);
    } else if (field == 'sublocality') {
      lineDom.push(<AddressSublocality address={props?.addressProps?.address} />);
    } else {
      lineDom.push(<>{field}</>)
    }
  });

  let modifiedAddressDom: JSX.Element[] = [];

  // if there is more than 1 address component per line, adds a space
  // without this logic: [["city", "regionCode"]] would look like:
  // ArlingtonVA
  for (var i = 0; i < lineDom.length; i++) {
    let e = lineDom[i]
    if ((i != 0) && e != null && (e.props?.children != ",")) {
      modifiedAddressDom.push(<>{' '}</>)
    }

    modifiedAddressDom.push(e)
  }

  return (
    <div className="Address-line" key={props?.line?.toString()}>
      {modifiedAddressDom}
    </div>
  );
}

const AddressLine1 = (props: AddressProps) => {
  return props.address?.line1 ? (
  <span className="Address-field Address-line1" key="addressline1">
    {props.address.line1}
  </span> ) : null;
}

const AddressLine2 = (props: AddressProps) => {
  return props.address?.line2 ? (
  <span className="Address-field Address-line2" key="addressline2">
    {props.address.line2}
  </span> ) : null;
}

const AddressLine3 = (props: AddressProps) => {
  return props.address?.line3 ? (
  <span className="Address-field Address-line3" key="addressline3">
    {props.address.line3}
  </span> ) : null;
}

const AddressPostalCode = (props: AddressProps) => {
  return props.address?.postalCode ? (
  <span className="Address-field Address-postalCode" key="addresspostalcode">
    {props.address.postalCode}
  </span> ) : null;
}

const AddressSublocality = (props: AddressProps) => {
  return props.address?.sublocality ? (
  <span className="Address-field Address-sublocality" key="addresssublocality">
    {props.address.sublocality}
  </span> ) : null;
}

const AddressCity = (props: AddressProps) => {
  return props.address?.city ? (
  <span className="Address-field Address-city" key="addresscity">
    {props.address.city}
  </span> ) : null;
}

const AddressRegion = (props: any) => {
  let region = props.address?.region ? props.address.region : ""
  let regionName = props.address?.localizedRegionName ? props.address.localizedRegionName : region

  if (region != "") {
    if (props.abbr) {
      return <abbr className="Address-field Address-region Address-region--code"
      title={regionName}
      key="addressregion"
      >
        {region}
      </abbr>
    } else {
      return <span className="Address-field Address-region Address-region"
      title={regionName}
      key="addressregion"
      >
        {regionName}
      </span>
    }
  }
  return null
}

const AddressCountry = (props: any) => {
  let countryCode = props.address?.countryCode ? props.address.countryCode : ""
  let countryName = props.address?.localizedCountryName ? props.address.localizedCountryName : countryCode

  if (countryCode != "") {
    if (props.abbr) {
      return <abbr className="Address-field Address-country Address-country--code"
      title={countryName}
      key="addresscountry"
      >
        {countryCode}
      </abbr>
    } else {
      return <span className="Address-field Address-country Address-country"
      key="addresscountry"
      >
        {countryName}
      </span>
    }
  }
  return null
}


const AddressExtraDescription = (props: AddressProps) => {
  return props.address?.extraDescription ? (
  <span className="Address-field Address-extraDescription" key="addressextradescription">
    {props.address.extraDescription}
  </span> ) : null;
}


export default Address;
