import React, { useState } from 'react';
import classNames from "classnames";
import { isHREFProps, cta as makeCTA, getHref, isEmail, reverse } from './methods';
import { Yext, StandardProps } from '../../types';
import { CTA } from "@yext/types"
import { useGlobal } from '../../utils';

export interface LinkConfig extends React.DetailedHTMLProps<React.AnchorHTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement>, StandardProps {
  linkType?: Yext.LinkType
  obfuscate?: boolean
}
export interface HREFLinkProps extends LinkConfig {
  href: string
  cta: never
}
export interface CTALinkProps extends LinkConfig {
  href: never
  cta: CTA
}

export type LinkProps = CTALinkProps | HREFLinkProps

// Initialize variable early so `defaultProps` can be set, to
// get around forwardRef being an annonymous function that
// returns a react function (instead of the typical being a
// react function).
let Link: any | React.ForwardRefRenderFunction<HTMLAnchorElement, LinkProps>;

/**
 * Links are accessible elements used primarily for navigation
 * 
 * @example
 * ```
 * <Link link="/search">Locator</Link>
 * ```
 *
 * @param {Yext.CTA} cta
 * @param {string} href
 * @param {boolean} [obfuscate]
 * 
 * @returns {Link}
 */
Link = React.forwardRef<HTMLAnchorElement, LinkProps>((props, ref) => {
  Link.defaultProps = useGlobal().components.Link;
  Link.displayName = 'Link';

  const {children, onClick, yaTrack, className, cta, href, ...rest} = props;
  const link: CTA = isHREFProps(props)
    ? makeCTA(href, props.linkType)
    : cta;

  const obfuscate = props.obfuscate || (props.obfuscate !== false && isEmail(link.link));
  const [humanInteraction, setHumanInteraction] = useState<boolean>(false);

  const handleClick = (e) => {
    setHumanInteraction(true);
    if (onClick) onClick(e);
  }

  const useLinkAsLabel = !children && !link.label;
  const isObfuscate = !humanInteraction && obfuscate !== false;
  const obfuscatedStyle: React.CSSProperties = {
    ...props.style,
    unicodeBidi: 'bidi-override',
    direction: useLinkAsLabel && isObfuscate ? 'rtl' : 'ltr',
  };

  const renderedLink = isObfuscate ? reverse(link.link) : link.link;

  return (
    <a
      className={classNames("Link", className)}
      data-ya-track={yaTrack}
      href={humanInteraction || !obfuscate ? getHref(link) : 'obfuscate'}
      onClick={(e) => handleClick(e)}
      rel={(props.target && !props.rel) ? 'noopener' : undefined}
      ref={ref}
      style={obfuscatedStyle}
      {...rest}
    >
      {children || link.label || renderedLink}
    </a>
  )
});

export default Link;
