import React, { createContext, useContext, useEffect, useRef, useState } from "react";
import { MapOptions } from "@yext/components-tsx-maps";
import type { Coordinate as CoordinateType } from "@yext/types";
import type { GeoBounds } from "@yext/components-tsx-geo";
import type { Map as MapType } from "@yext/components-tsx-maps";
import type { MapContextType, MapProps, MapDefaultProps } from "./Map.types";
import { Coordinate } from "@yext/components-tsx-geo";

export const MapContext = createContext<MapContextType | null>(null);

// Hook that can be used to access the map value within children of the Map component
export function useMapContext() {
  const mapContext = useContext(MapContext);
  if (!mapContext || mapContext.map === undefined) {
    throw new Error('Attempted to call useMapContext() outside of Map.');
  }

  return mapContext.map;
}

const defaultProps: MapDefaultProps = {
  controlEnabled: true,
  defaultCenter: { latitude: 39.83, longitude: -98.58 },
  defaultZoom: 4,
  padding: { bottom: () => 50, left: () => 50, right: () => 50, top: () => 50 },
  panHandler: (previousBounds, currentBounds) => {},
  panStartHandler: (currentBounds) => {},
  providerOptions: {},
  singlePinZoom: 14,
};

export const Map = (props: MapProps & MapDefaultProps) => {
  const { apiKey, bounds, clientKey, controlEnabled, defaultCenter, defaultZoom, padding, panHandler, panStartHandler, provider, providerOptions, singlePinZoom } = props;
  const mapWrapper = useRef<HTMLDivElement>(null);
  const [map, setMap] = useState<MapType>();
  const [mapLoaded, setMapLoaded] = useState<boolean>(false);

  // Controlled properties that need to update on user interaction
  const [center, setCenter] = useState<CoordinateType>(defaultCenter);
  const [zoom, setZoom] = useState<number>(defaultZoom);

  // Call user defined panHandler and set center state on map move
  const _panHandler = (previousBounds: GeoBounds, currentBounds: GeoBounds) => {
    panHandler(previousBounds, currentBounds);
    const centerCoordinate = currentBounds.getCenter();
    const centerLat = centerCoordinate.latitude;
    const centerLng = centerCoordinate.longitude;
    setCenter({ latitude: centerLat, longitude: centerLng });
  }

  // On center change get new zoom from map and set zoom state
  useEffect(() => {
    if (!mapLoaded || !map) return;
    const zoom = map.getZoom();
    setZoom(zoom);
  }, [center]);

  // Fit map to bounds when bounds updated or when map initialized
  useEffect(() => {
    if (!bounds || !mapLoaded || !map) return;
    const coordinates = bounds.map(bound => new Coordinate(bound));
    map.fitCoordinates(coordinates);
  }, [JSON.stringify(bounds), map]);

  useEffect(() => {
    if (mapLoaded || map || !mapWrapper.current) return;
    loadMap();
  }, []);

  useEffect(() => {
    if (!mapLoaded || map) return;
    const providerMap = new MapOptions()
      .withControlEnabled(controlEnabled)
      .withDefaultCenter(center)
      .withDefaultZoom(zoom)
      .withPadding(padding)
      .withPanHandler(_panHandler)
      .withPanStartHandler(panStartHandler)
      .withProvider(provider)
      .withProviderOptions(providerOptions)
      .withSinglePinZoom(singlePinZoom)
      .withWrapper(mapWrapper.current)
      .build();
    setMap(providerMap);
  }, [mapLoaded]);

  const loadMap = () => {
    const providerName = provider.getProviderName();
    const loadOptions = providerName === 'Google' ? {client: clientKey} : {};

    provider.load(apiKey, {
      ...loadOptions
    }).then(() => {
      setMapLoaded(true);
    });
  }

  return (
    <div className="dir-map" id="map" ref={mapWrapper}>
      {map && (
        <MapContext.Provider value={{
          map: map,
          provider: provider,
        }}>
          {props.children}
        </MapContext.Provider>
      )}
    </div>
  )
}

Map.defaultProps = defaultProps;

export default Map;
