import React from "react";
import { LocationMapDefaultProps, LocationMapProps } from "./LocationMap.types";
import { Map, Marker } from "../Map";
import Link from "../Link";

const defaultProps: LocationMapDefaultProps = {
  controlEnabled: false,
  markerClickHandler: (id) => {},
  markerHoverHandler: (hovered, id) => {},
  markerFocusHandler: (focused, id) => {},
};

const LocationMap = (props: LocationMapProps & LocationMapDefaultProps) => {
  const { children, coordinate, controlEnabled, linkSameTab, pinUrl, markerClickHandler, markerHoverHandler, markerFocusHandler, ...rest} = props;

  return (
    <Map bounds={[coordinate]} controlEnabled={controlEnabled} {...rest}>
      <Marker coordinate={coordinate} id={'location-map-marker'} markerClickHandler={markerClickHandler} markerHoverHandler={markerHoverHandler} markerFocusHandler={markerFocusHandler}>
        <>
          {pinUrl && (
            <Link href={pinUrl} target={linkSameTab ? "_self" : "_blank"}>
              {children}
            </Link>
          )}
          {!pinUrl && children}
        </>
      </Marker>
    </Map>
  )
}

LocationMap.defaultProps = defaultProps;

export default LocationMap;
