import React, { ReactNode } from 'react';
import classNames from "classnames";
import { StandardProps } from '../../types';
import Link from '../Link/Link';
import { cta } from '../Link/methods';
import './Breadcrumbs.css';

export interface BreadcrumbsProps extends StandardProps {
  separator?: ReactNode
  className?: string
}


export interface BreadcrumbsPropsDefault extends BreadcrumbsProps {
  breadcrumbs: Array<[string, string]>;
}

/*
 * The 'Breadcrumbs' component renders a breadcrumbs style navigation bar
 *
 * @example
 * ```
 * <Breadcrumbs breadcrumbs={defaultTransformer(streamBreadcrumbs)} />
 * ```
 * 
 * @param {[[string, string]]} breadcrumbs
 *          List of [crumbName, crumbUrl]
 * @param {ReactElement | string} separator
 *          between each breadcrumb - defaults to '/', also accepts any HTML element (<svg>, <img>, etc)
 */
const Breadcrumbs = (props: BreadcrumbsPropsDefault) => {
  const {
    breadcrumbs,
    yaScope,
    className,
    separator='/'
  } = props;

  return (
    <nav className={classNames('Breadcrumbs', className)}
         data-ya-scope={yaScope || 'breadcrumbs'}
         aria-label="Breadcrumb">
      <ol className="Breadcrumbs-list">
        {breadcrumbs.map(([label, link], idx) => {
          const isLast = idx ===(breadcrumbs.length - 1);

          return (
            <li className="Breadcrumbs-item" key={idx}>
              <Breadcrumb label={label} link={link} {...props} />
              {!isLast && <span className="Breadcrumbs-separator">{separator}</span>}
            </li>
          )
        })}
      </ol>
    </nav>
  )
}

interface BreadcrumbProps {
  label: string;
  link?: string
  yaTrack?: string
}

const Breadcrumb = (props: BreadcrumbProps) => {
  const { label, link, yaTrack } = props;

  let breadcrumbEl = 
    <span className="Breadcrumbs-label">
      {label}
    </span>;

  if (link) {
    breadcrumbEl = 
      <Link className="Breadcrumbs-link Link--breadcrumbs" cta={cta(link)} yaTrack={yaTrack}>
        {breadcrumbEl}
      </Link>
  }

  return breadcrumbEl;
}

export default Breadcrumbs;