import { StreamsBreadcrumbs } from './YextBreadcrumbs';

/*
 * defaultTransformer transforms the breadcrumbs data from the Yext Streams default format to
 *  the format accepted by the <Breadcrumbs> component.
 */
const defaultTransformer = (breadcrumbsData: StreamsBreadcrumbs): Array<[string, string]> => {
  return  breadcrumbsData.dm_directoryParents.map(
    breadcrumbData => [breadcrumbData.name, breadcrumbData.slug]
  );
}

export {
  defaultTransformer,
}
