import { ReactNode } from 'react';
import PhoneNumber from 'awesome-phonenumber';
import { CTA } from "@yext/types";
import { HREFLinkProps, LinkProps } from './Link';

enum LinkType {
  URL = 'URL',
  Email = 'Email',
  Phone = 'Phone',
}

/**
 * Get the link from a CTA
 * 
 * @param {CTA} cta
 *
 * @returns {string}
 */
const getHref = (cta: CTA): string => {
  if (cta.linkType === 'Email' || isEmail(cta.link)) {
    return `mailto:${cta.link}`;
  }

  if (cta.linkType === 'Phone' || isPhone(cta.link)) {
    return `tel:${cta.link}`;
  }

  return cta.link;
}

/**
 * CTA constructor
 * 
 * @param {string} link
 * @param {LinkType} linkType
 * @param {string} label
 *
 * @returns {boolean}
 */
export function cta(link: string, linkType: LinkType | string = LinkType.URL, label?: ReactNode): CTA {

  
  return {
    link,
    linkType: parseLinkType(linkType),
    label: typeof label === 'string' ? label : '',
  }
}

export function parseLinkType(type: LinkType | string): LinkType {
  if (typeof type !== 'string') return type;

  switch (type) {
    case 'Email': return LinkType.Email;
    case 'Phone': return LinkType.Phone;
    default: return LinkType.URL;
  }
}

/**
 * Type predicate for distinguishing different prop types
 * 
 * @param {LinkProps} props
 *
 * @returns {CTA}
 */
export function isHREFProps(props: LinkProps): props is HREFLinkProps {
  return ("href" in props);
}

/**
 * Checks if is a valid email address
 * 
 * Regex defined in HTML Spec for <input type="email"> validation.
 * https://html.spec.whatwg.org/#email-state-(type=email)
 */
const isEmail = (string: string): boolean => {
  const re = /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
  return re.test(string);
}

/**
 * Checks if is a valid phone number using libphonenumber
 * 
 * HTML Spec does not define telephone validation.
 * https://html.spec.whatwg.org/#telephone-state-(type=tel)
 */
const isPhone = (string: string): boolean => {
  const phoneNumber = new PhoneNumber(string); // TODO: pass regionCode as second param
  return phoneNumber.isValid();
}

/**
 * Reverse a string, used for obfuscation
 * 
 * https://eddmann.com/posts/ten-ways-to-reverse-a-string-in-javascript/
 */
const reverse = (string: string): string => {
  for (var i = string.length - 1, o = ''; i >= 0; o += string[i--]) { }
  return o;
}

export {
  getHref,
  isEmail,
  isPhone,
  reverse,
}
