import React, { useContext, useEffect, useMemo, CSSProperties } from "react";
import { createPortal } from "react-dom";
import { MapContext } from "./Map";
import type { MapContextType } from "./Map.types";
import type { MarkerDefaultProps, MarkerProps } from "./Marker.types";
import { MapPinOptions, PinProperties } from "@yext/components-tsx-maps";
import type { MapPin } from "@yext/components-tsx-maps";

const defaultProps: MarkerDefaultProps = {
  markerClickHandler: (id) => {},
  markerHoverHandler: (hovered, id) => {},
  markerFocusHandler: (focused, id) => {},
  markerStatusOptions: {},
};

const defaultStyle: CSSProperties = {
  position: "absolute",
  bottom: 0,
  left: "50%",
  transform: "translateX(-50%)",
}

export const Marker = ({ children, coordinate, hideOffscreen, id, mapPinOptions, markerClickHandler, markerFocusHandler, markerHoverHandler, markerStatusOptions, propertiesForStatus, zIndex }: MarkerProps) => {
  const { map, provider }  = useContext(MapContext) as MapContextType;

  const marker: MapPin = useMemo(() => {
    if (children && coordinate) {
      return new MapPinOptions()
        .withCoordinate(coordinate)
        .withHideOffscreen(hideOffscreen ? hideOffscreen : false)
        .withPropertiesForStatus(propertiesForStatus ? propertiesForStatus : () => new PinProperties())
        .withProvider(provider)
        .build();
    } else if (mapPinOptions) {
      return mapPinOptions
        .withProvider(provider)
        .build();
    }
    return null;
  }, []);

  // Update MapPin status.
  // Use with propertiesForStatus or mapPinOptions to update property based on status if needed (for example a non-HTMLProviderPin)
  useEffect(() => {
    marker.setStatus({ ...markerStatusOptions });
  }, [markerStatusOptions]);

  // Set zIndex of wrapperElement HTMLProviderPin is rendered into
  // Incompatible with markerStatusOptions as that will update the MapPin status and reset zIndex to 0
  useEffect(() => {
    if (zIndex !== 0 && !zIndex) return;
    const markerWrapper = marker.getProviderPin().getWrapperElement();
    markerWrapper.style.zIndex = zIndex;
  }, [zIndex]);

  useEffect(() => {
    marker.setMap(map);
    marker.setClickHandler(() => { markerClickHandler(id) });
    marker.setHoverHandler((hovered: boolean) => { markerHoverHandler(hovered, id) });
    marker.setFocusHandler((focused: boolean) => { markerFocusHandler(focused, id) });

    return () => {
      marker.setMap(null);
    }
  }, []);

  if (children) {
    // Render children into element created by HTMLProviderPin
    const pinEl = marker.getProviderPin().getPinElement();
    // Position child inside parent so marker is visually at the correct coordinate
    return createPortal(<div style={defaultStyle}>{children}</div>, pinEl);
  }

  return null;
}

Marker.defaultProps = defaultProps;
